<template>
  <div id="band" class="div-relative ">
    <nav class="navbar  bg-primary fixed-top navbar-transparent " color-on-scroll="400">
      <div class="container">
        <div>
          <ul>
          </ul>
        </div>
      </div>
    </nav>

    <div class="page-header div-a" filter-color="orange">
      <!--<div class="page-header-image"></div>-->
      <div class="container">
        <div class="content-center">
          <div class="card">
            <div slot="title" style="margin-bottom:10px;">
              <div style="margin-left:30px;display:inline-block">
                <img src="./assets/logo_p.png" width="60">
              </div>
              <div style="display:inline-block;margin-left:10px;">
                <span style="font-size: 2em;">Pallas</span>
              </div>
            </div>
            <form v-if="isPassport !== '1'" style="width: 45%;margin-left: 25%">
              <div class="input-group form-group-no-border input-lg">
                                <span class="input-group-addon">
                                   <Icon type="md-person"/>
                                </span>
                <input type="text" class="form-control" placeholder="账号" v-model="formInline.user" :readonly="buttonLoading">
              </div>
              <div class="input-group form-group-no-border input-lg">
                                <span class="input-group-addon">
                                  <Icon type="md-key"/>
                                </span>
                <input type="password" placeholder="密码" class="form-control" :readonly="buttonLoading"
                       v-model="formInline.password"
                       @keyup.enter="authdata"/>
              </div>
              <div class="footer text-center">
                <Button class="btn btn-primary btn-round btn-lg btn-block" shape="circle" :loading="buttonLoading" @click="authdata()">登录</Button>
              </div>
            </form>
          </div>
        </div>
      </div>
      <footer class="footer">
        <div class="container">
        </div>
      </footer>
    </div>

  </div>
</template>
<script>
    import { getUserInfo, getLocalUserInfo } from '@/api/login';

    export default {
      name: 'Login',
      data() {
        return {
          buttonLoading: false,
          switchCode: false,
          token: '',
          isPassport: process.env.VUE_APP_IS_PASSPORT,
          userinfoValidate: {
            username: [
              {
                required: true,
                message: '请输入用户名',
                trigger: 'blur'
              }
            ],
            password: [
              {
                required: true,
                message: '请输入密码',
                trigger: 'blur'
              },
              {
                min: 6,
                message: '请至少输入6个字符',
                trigger: 'blur'
              },
              {
                max: 32,
                message: '最多输入32个字符',
                trigger: 'blur'
              },
              {
                trigger: 'blur'
              }
            ],
            confirmpassword: [
              {
                required: true,
                message: '请再次输入新密码',
                trigger: 'blur'
              },
              {
                trigger: 'blur'
              }
            ],
            realname: [
              {
                required: true,
                message: '请输入真实姓名',
                trigger: 'blur'
              }
            ],
            department: [
              {
                required: true,
                message: '请输入部门名称',
                trigger: 'blur'
              }
            ],
            email: [
              {required: true, message: '请输入邮箱地址', trigger: 'blur'},
              {type: 'email', message: '邮箱格式错误', trigger: 'blur'}
            ]
          },
          formInline: {
            user: '',
            password: ''
          },
          ruleInline: {
            user: [{
              required: true,
              message: '请填写用户名',
              trigger: 'blur'
            }],
            password: [{
              required: true,
              message: '请填写密码',
              trigger: 'blur'
            },
              {
                type: 'string',
                min: 6,
                message: '密码长度不能小于6位',
                trigger: 'blur'
              }
            ]
          }
        }
      },
      methods: {
        autoLogout(showErrorMsg) {
          if (showErrorMsg) {
            // this.$Message.error({content: '未能获取用户信息，正在自动退出', duration: 5})
          }
          if (window.location.origin.indexOf('pallas-test') >= 0) {
            location.href = `${process.env.VUE_APP_LUFAX_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_LUFAX_TEST_BASE_URL}`;
          } else if(window.location.origin.indexOf('devops') >= 0) {
            location.href = `${process.env.VUE_APP_LUFAX_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_LUFAX_BASE_URL}`;
          } else if (window.location.origin.indexOf('lujs') >= 0) {
            location.href = `${process.env.VUE_APP_LUJS_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_LUJS_BASE_URL}`;
          } else if (window.location.origin.indexOf('trust') >= 0) {
            location.href = `${process.env.VUE_APP_TRUST_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_TRUST_BASE_URL}`;
          } else if (window.location.origin.indexOf('172') >= 0) {
            location.href = `${process.env.VUE_APP_DEV_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_DEV_BASE_URL}`;
          } else if (window.location.origin.indexOf('5.6') >= 0) {
            location.href = `${process.env.VUE_APP_DEV_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_DEV_BASE_URL}`;
          } else if (window.location.origin.indexOf('.94.') >= 0) {
            location.href = `${process.env.VUE_APP_PRE_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_PRE_BASE_URL}`;
          } else if (window.location.origin.indexOf('30.23') >= 0) {
            location.href = `${process.env.VUE_APP_OA_PRE_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_OA_PRE_BASE_URL}`;
          } else {
            this.$Message.error({content: '未登记的登录环境，请联系管理员', duration: 15})
          }
        },
        login () {
          getUserInfo(this.token).then(res => {
            console.log(res);
            if (res.data.code === 200) {
              sessionStorage.setItem('user', res.data.data.um);
              sessionStorage.setItem('token', this.token);
              sessionStorage.setItem('real_name', res.data.data.name);
              sessionStorage.setItem('tenant_name', res.data.data.tenant_des);
              sessionStorage.setItem('tenant_id', res.data.data.tenant_id);
              let _auth = new Set(res.data.data.permissions.join('.').split('.'))
              if (res.data.data.permissions.indexOf('admin.admin') >= 0) {
                sessionStorage.setItem('access', 1)
                sessionStorage.setItem('auth', Array.from(_auth).join(','));
              } else {
                sessionStorage.setItem('access', 0)
                sessionStorage.setItem('auth', Array.from(_auth).join(','));
              }
              sessionStorage.setItem('department', res.data.data.department.join(',') ? res.data.data.department: '无');
              sessionStorage.setItem('email', res.data.data.email);
              sessionStorage.setItem('show_count', '1');
              // this.$router.push({
              //   name: 'home_index'
              // })
              location.href = `${window.location.origin}#/home`;
            } else {
              this.autoLogout(true)
            }
          }).catch(err => {
            this.$config.auth_notice(err)
          });
        },
        getUrlParameter (name) {
          name = name.replace(/\[]/, '\\[').replace(/[\]]/, '\\]');
          let regex = new RegExp('[\\?&]' + name + '=([^&#]*)');
          let results = regex.exec(location.search);
          return results === null ? '' : decodeURIComponent(results[1].replace(/\+/g, ' '));
        },
        authdata() {
          this.buttonLoading = true;
          getLocalUserInfo(this.formInline.user, this.formInline.password).then(res => {
            console.log(res)
            if (res.data.status === 'success') {

              this.$Message.success({content: '登录成功', duration: 4});
              sessionStorage.setItem('user', res.data.data.username);
              sessionStorage.setItem('token', res.data.token);
              sessionStorage.setItem('auth', res.data.data.role);
              sessionStorage.setItem('real_name', res.data.data.username_chn);
              sessionStorage.setItem('tenant_name', res.data.data.tenant);
              if (res.data.data.role === 'admin') {
                sessionStorage.setItem('access', 1)
              } else {
                sessionStorage.setItem('access', 0)
              }
              sessionStorage.setItem('department', res.data.data.department ? res.data.data.department: '无');
              sessionStorage.setItem('email', res.data.data.email);
              this.$router.push({
                name: 'home_index'
              })
            } else {
              this.$config.auth_notice('用户不存在或用户名密码错误。')
            }

          }).catch(err => {
            this.$config.auth_notice(err)
          }).finally(() => {
            this.buttonLoading = false;
          });

          // let url = this.$config.auth;
          // axios.post(url, {
          //     'username': this.formInline.user,
          //     'password': this.formInline.password
          // })
          //     .then(res => {
          //         axios.defaults.headers.common['Authorization'] = 'Bearer ' + res.data.token
          //         sessionStorage.setItem('user', this.formInline.user)
          //         sessionStorage.setItem('jwt', `Bearer ${res.data.token}`)
          //         sessionStorage.setItem('auth', res.data['permissions'])
          //         sessionStorage.setItem('real_name', res.data['real_name'])
          //         let auth = res.data['permissions']
          //         if (auth === 'admin' || auth === 'perform') {
          //             sessionStorage.setItem('access', 0)
          //         } else {
          //             sessionStorage.setItem('access', 1)
          //         }
          //         this.$router.push({
          //             name: 'home_index'
          //         })
          //     })
          //     .catch(err => {
          //         this.$config.auth_notice(err)
          //     }) [admin, user]split(.)
        }
      },
      mounted() {
        // window.particlesJS.load('band', `${process.env.BASE_URL}particlesjs-config.json`);
        // 注册用户开关
        this.token = this.getUrlParameter('_token').replace('/', '')
        this.switchCode = 1
        if (process.env.VUE_APP_IS_PASSPORT === '1') {
          if (sessionStorage.getItem('token') !== 'null' && sessionStorage.getItem('token') !== null) {
            console.log('check session token')
            getUserInfo(this.token).then(res => {
              if (res.data.code === 200) {
                this.$Message.success({content: '登录成功', duration: 5});
                sessionStorage.setItem('user', res.data.data.um);
                sessionStorage.setItem('token', this.token);
                sessionStorage.setItem('real_name', res.data.data.name);
                sessionStorage.setItem('tenant_name', res.data.data.tenant_des);
                sessionStorage.setItem('tenant_id', res.data.data.tenant_id);
                let _auth = new Set(res.data.data.permissions.join('.').split('.'))
                if (res.data.data.permissions.indexOf('admin.admin') >= 0) {
                  sessionStorage.setItem('access', 1)
                  sessionStorage.setItem('auth', Array.from(_auth).join(','));
                } else {
                  sessionStorage.setItem('access', 0)
                  sessionStorage.setItem('auth', Array.from(_auth).join(','));
                }
                sessionStorage.setItem('department', res.data.data.department.join(',') ? res.data.data.department: '无');
                sessionStorage.setItem('email', res.data.data.email);
                sessionStorage.setItem('show_count', '1');
                // sessionStorage.setItem('show_count', res.data.data.show_count.toString());
                // this.$router.push({
                //   name: 'home_index'
                // })
                location.href = `${window.location.origin}#/home`;
              } else {
                this.autoLogout(true)
              }
            }).catch(err => {
              this.$config.auth_notice(err)
            });
          } else if (!this.token) {
            this.autoLogout(true)
          } else {
            this.login()
          }
        }
      }
    }
</script>
